}

.u-edit .edit-wrap .item {
  padding: 20px 0 !important;
  border-bottom: 1px solid rgba(34, 36, 38, 0.15);
  font-size: 15px;
  color: #333;
}

.u-edit .edit-wrap .item:last-child {
  border-bottom: none;
}

.u-edit .head-icon {
